.calendar {
    display: flex;
    flex-wrap: wrap;
    gap: $blockGap;
  
    padding: $blockPadding;
    max-width: getWidth($blockSize, $blockGap, $blockPadding);
  
    @each $day, $position in $week {
      &--start-day_#{$day} :first-child {
        margin-left: ($blockSize + $blockGap) * $position;
      }
    }
  
    @for $index from 1 through 31 {
      &--month-length_#{$index} :nth-child(n + #{$index + 1}) {
        display: none;
      }
    }
  
    &__day {
      @include daySize($blockSize);
  
      display: flex;
      background-color: #eee;
      border: 1px solid #000;
    
      font-family: Arial, Helvetica, sans-serif;
      font-size: 30px;
      cursor: pointer;
  
      transition-duration: 0.5s;
   
      @for $index from 1 through 31 {
        &:nth-child(#{$index})::before {
          content: "#{$index}";
        }
      }
  
      &::before {
        width: 100%;
      
        display: flex;
        align-self: center;
        justify-content: center;
      }
  
      &:hover {
        transform: translateY(-20px);
        background-color: pink;
      }
      
    }
  }
  